import localeStrings from '../../../public/locales/cs/application.json';
import { CardResponsive } from '../ResponsiveContainers/CardResponsive';
import { isDeviceTablet, useWindowSize } from '@flexi-monorepo/common-ui';
import { useOffer } from '@flexi-monorepo/novaflexi/offer';
import {
  Client,
  CreateBasicDataProductIdEnum,
  useAPIs,
} from '@flexi-monorepo/primary-api';
import { YesNoToggleButtons } from '@flexi-monorepo/react-hooks-form-gds-binding';
import {
  CheckboxInput,
  FormField,
  Heading,
  Icon,
  Paragraph,
  Row,
  Separator,
} from '@george-labs.com/design-system';
import React, { useState } from 'react';
import { Controller, useFormContext } from 'react-hook-form';
import { Trans, useTranslation } from 'react-i18next';

interface IProps {
  clientDetail: Client | undefined;
}

export const Confirmation: React.FC<IProps> = ({ clientDetail }) => {
  const { offerApi } = useAPIs();
  const { device } = useWindowSize();
  const [isModelationSent, setIsModelationSent] = useState(false);
  const {
    control,
    formState: { errors },
    watch,
    setValue,
  } = useFormContext();
  const { t } = useTranslation(['application']);

  function hasError() {
    return errors['confirmation']?.message !== undefined;
  }

  function hasEmailError() {
    return errors['confirmationEmail']?.message !== undefined;
  }

  const offer = useOffer();

  async function sendModelation(event: any) {
    try {
      if (!isModelationSent && event.target.checked) {
        await offerApi.sendModelation({
          offerId: offer.data!.offerResponse.id!,
          productId: CreateBasicDataProductIdEnum.K1Fn,
          requiredByClient: false,
        });
        setIsModelationSent(true);
      }
    } catch (e) {
      console.error(e);
      setIsModelationSent(false);
    }
  }

  return (
    <>
      <CardResponsive className={'px-md-5 py-md-0'}>
        <Row className={'align-items-center'}>
          <div className={'ml-3 mr-2'}>
            <Icon
              icon={Icon.ICON.THUMB_UP}
              color={Icon.COLOR.BLUE_400}
              size={
                isDeviceTablet(device) ? Icon.SIZE.SIZE_24 : Icon.SIZE.SIZE_16
              }
            />
          </div>
          <div className={'pt-1 h-4'}>
            <Heading level={3}>
              {
                localeStrings.additionalInfo.inputs.confirmationsAndAgreements
                  .title
              }
            </Heading>
          </div>
        </Row>
      </CardResponsive>
      <CardResponsive className={'px-3 px-md-5 py-md-0'}>
        <div className="row col-12 px-md-5 pb-md-2">
          <Paragraph>
            <Trans
              t={t}
              i18nKey={
                localeStrings.additionalInfo.inputs.confirmationsAndAgreements
                  .marketingCommunicationConsent.description
              }
            />
          </Paragraph>
        </div>
        <div className={'row col-12 px-md-4'}>
          <Controller
            name={'marketingCommunicationConsent'}
            control={control}
            render={({ field }) => (
              <FormField
                defaultChecked={false}
                {...field}
                inputProps={{
                  innerRef: field.ref,
                  ref: undefined,
                }}
                labelColumnWidth={0}
                error={
                  hasError()
                    ? localeStrings.additionalInfo.inputs
                        .confirmationsAndAgreements
                        .marketingCommunicationConsent
                        .confirmationNotCheckedErrorMessage
                    : undefined
                }
                // @ts-ignore
                input={(inputProps) => (
                  <div className={'px-md-2'}>
                    <CheckboxInput
                      name={field.name}
                      data-cy="checkbox/nechci-obchodní-sdělení"
                      onChange={(...e) => {
                        field.onChange(...e);
                        if (e[0].target.checked)
                          setValue('thirdPartyPropositionConsent', false);
                      }}
                      checked={inputProps.value === true}
                      // defaultChecked={inputProps.value === true}
                      label={
                        localeStrings.additionalInfo.inputs
                          .confirmationsAndAgreements
                          .marketingCommunicationConsent.label
                      }
                    />
                  </div>
                )}
                // @ts-ignore
                ref={undefined}
              />
            )}
          />
        </div>
      </CardResponsive>
      <CardResponsive className={'px-3 px-md-5 py-md-0'}>
        <div className={'row col-12 px-md-4'}>
          <YesNoToggleButtons
            required
            data-cy="nabídky-na-míru?"
            name="thirdPartyPropositionConsent"
            label={
              localeStrings.additionalInfo.inputs.confirmationsAndAgreements
                .thirdPartyPropositionConsent.label
            }
            labelYes={
              localeStrings.additionalInfo.inputs.confirmationsAndAgreements
                .thirdPartyPropositionConsent.yes
            }
            labelNo={
              localeStrings.additionalInfo.inputs.confirmationsAndAgreements
                .thirdPartyPropositionConsent.no
            }
            disabled={!!watch('marketingCommunicationConsent')}
            className={'px-md-2'}
            // @ts-ignore
            classNameInput={' pt-2  pr-md-2 pt-md-0'}
            labelColumnWidth={7}
          />
        </div>
      </CardResponsive>
      <Separator></Separator>
      <CardResponsive className={'px-3 px-md-5 py-md-0'}>
        <div className={'row col-12 px-md-4'}>
          <Controller
            name={'confirmationEmail'}
            control={control}
            render={({ field }) => (
              <FormField
                defaultChecked={false}
                labelColumnWidth={0}
                error={
                  hasEmailError()
                    ? localeStrings.additionalInfo.inputs.confirmationEmail
                        .confirmationEmailNotCheckedErrorMessage
                    : undefined
                }
                input={(inputProps: any) => (
                  <div data-cs-mask className={'px-md-2'}>
                    <CheckboxInput
                      name={field.name}
                      data-cy="checkbox/zaslání-modelace"
                      onChange={(e) => {
                        field.onChange(e);
                        sendModelation(e);
                      }}
                      checked={inputProps.value === true}
                      label={
                        localeStrings.additionalInfo.inputs.confirmationEmail
                          .label + clientDetail?.contacts?.email
                      }
                    ></CheckboxInput>
                  </div>
                )}
              ></FormField>
            )}
          ></Controller>
        </div>
      </CardResponsive>
    </>
  );
};
