import { useIsFaceliftVisible } from '../../useIsFaceliftVisible';
import InfoModal from '../InfoModal/InfoModal';
import { CardResponsive } from '../ResponsiveContainers/CardResponsive';
import { HighRiskCountryAlert } from './HighRiskCountryAlert';
import { HighlyInsuredAlert } from './HighlyInsuredAlert';
import { isDeviceTablet, useWindowSize } from '@flexi-monorepo/common-ui';
import {
  useWatchHighRiskCountry,
  useWatchPEP,
} from '@flexi-monorepo/novaflexi/additional-info';
import { useOffer } from '@flexi-monorepo/novaflexi/offer';
import { fullQuestionnaireJson } from '@flexi-monorepo/novaflexi/questionnaire';
import {
  CreateOfferProductIdEnum,
  RequiredDocumentDtoIdEnum,
  RequiredDocuments,
  useGetRequestedDocuments,
} from '@flexi-monorepo/primary-api';
import {
  Alert,
  Button,
  Col,
  Heading,
  Icon,
  Paragraph,
  Row,
} from '@george-labs.com/design-system';
import React, { useState } from 'react';

function isMedicalExamination(id: string) {
  return id === 'PROHLIDKA_INTERNI' || id === 'PROHLIDKA_INTERNI_1';
}

/**
 * Využívá datovou strukturu množiny, abych zajistil, že každý dokument bude právě jeden. Díky tomu nemusím seznam
 * dokumentů z BE prohledávat, jestli už náhodou neobsahuje PrijemPotvrzeni nebo FinancniDotaznik, protoze to za mě
 * udělá množina sama.
 * Bohužel množina neumožňuje změnit, jak prvky porovnává, takže do množiny nasypu jen IDčka. Na konci IDčka namapuji
 * přes slovník opět na objekty v původním tvaru
 * @param data
 * @param pep
 * @param isFromHighRiskCountry
 * @param isFacelift
 */
export function modifyDocumentsListToTakeIntoAccountPEP(
  data: RequiredDocuments,
  pep: boolean,
  isFromHighRiskCountry: boolean,
  isFacelift: boolean
) {
  const nonEmptyDocuments = data.requiredDocuments ?? [];

  const documentIDS = new Set(nonEmptyDocuments.map((doc) => doc.id!));
  const documentTitles = nonEmptyDocuments.reduce(
    (acc, doc) => ({ ...acc, [doc.id!]: doc.name }),
    {} as Record<RequiredDocumentDtoIdEnum, string>
  );

  if (isFacelift) {
    documentTitles[RequiredDocumentDtoIdEnum.PrijemPotvrzeni] =
      'Příjem - potvrzení';
    documentTitles[RequiredDocumentDtoIdEnum.FinancniDotaznik] =
      'Finanční dotazník';

    if (pep) {
      documentIDS.add(RequiredDocumentDtoIdEnum.PrijemPotvrzeni);
      documentIDS.add(RequiredDocumentDtoIdEnum.FinancniDotaznik);
    }

    if (isFromHighRiskCountry) {
      documentIDS.add(RequiredDocumentDtoIdEnum.PrijemPotvrzeni);
    }
  }

  return Array.from(documentIDS).map((docid) => ({
    id: docid,
    name: documentTitles[docid],
  }));
}

/**
 * @return transformed documents list. Adds PrijemPotvrzeni and FinancniDotaznik documents in case their missing in BE response and user is PEP
 */
function useDocumentsPEPHighRiskCountry() {
  const offer = useOffer();
  if (!offer.data?.offerResponse.id)
    throw new Error('missing offerResponse.id');

  const pep = useWatchPEP();
  const isFromHighRiskCountry = useWatchHighRiskCountry();
  const isFaceliftVisible = useIsFaceliftVisible();

  return useGetRequestedDocuments(
    {
      productId: CreateOfferProductIdEnum.K1Fn,
      offerId: offer.data?.offerResponse.id,
    },
    {},
    {
      select(data) {
        return modifyDocumentsListToTakeIntoAccountPEP(
          data,
          pep,
          isFromHighRiskCountry,
          isFaceliftVisible
        );
      },
    }
  );
}

export const AdditionalDocuments = () => {
  const { device } = useWindowSize();
  const {
    fullQuestionnaire: {
      modal: { requestedHealthDocuments },
    },
  } = fullQuestionnaireJson;
  const [displayModal, setDisplayModal] = useState(false);

  const requiredDocuments = useDocumentsPEPHighRiskCountry();
  const documents = requiredDocuments?.data;

  return (
    <>
      <InfoModal
        displayModal={displayModal}
        setDisplayModal={setDisplayModal}
        type={'medicalExamination'}
      />
      <CardResponsive className={'px-md-5 py-md-0'}>
        <Row className={'align-items-center'}>
          <div className={'ml-3 mr-2'}>
            <Icon
              icon={Icon.ICON.FILE}
              color={Icon.COLOR.BLUE_400}
              size={
                isDeviceTablet(device) ? Icon.SIZE.SIZE_24 : Icon.SIZE.SIZE_16
              }
            />
          </div>
          <div className={'pt-1 h-4'}>
            <Heading level={3}>{requestedHealthDocuments.title}</Heading>
          </div>
        </Row>
      </CardResponsive>
      <CardResponsive className={'px-md-5 py-md-0'}>
        <HighRiskCountryAlert />
        <HighlyInsuredAlert />
        {documents && documents.length ? (
          <Row>
            <Col className={'pl-0'}>
              {Array.from(documents).map((item) => {
                return (
                  <div className={'d-flex flex-row'} key={item.id}>
                    <Paragraph data-cy={item.id} className={'ml-4 ml-md-5'}>
                      {item.name}
                    </Paragraph>
                    {isMedicalExamination(item.id) && (
                      <div className="ml-2 mb-n1 align-items-center">
                        <Button
                          variant={Button.VARIANT.TERTIARY}
                          icon={Button.ICON.INFO}
                          className={'p-0 m-0 mt-n1'}
                          onClick={() => setDisplayModal(true)}
                          data-cy={
                            'dokumenty-k-doložení/lékařská-prohlídka/otevřít-modál'
                          }
                        />
                      </div>
                    )}
                  </div>
                );
              })}
            </Col>
          </Row>
        ) : (
          <Col className={'pr-md-5'}>
            <Alert
              size={Alert.SIZE.SMALL}
              variant={Alert.VARIANT.INFO}
              className={'ml-md-3'}
            >
              <Paragraph>{requestedHealthDocuments.noDocuments}</Paragraph>
            </Alert>
          </Col>
        )}
      </CardResponsive>
    </>
  );
};
