import { useWindowSize, isDeviceTablet } from '@flexi-monorepo/common-ui';
import React from 'react';

interface IProps {
  children: Array<JSX.Element>;
  footer?: React.ReactNode;
}

function hasMultipleChildren(children: JSX.Element | Array<JSX.Element>) {
  return React.Children.count(children);
}

export const ContainerResponsive: React.FC<IProps> = ({ children, footer }) => {
  const { device } = useWindowSize();
  return (
    <>
      <div
        style={{
          backgroundColor: 'white',
          borderRadius: '8px',
          marginTop: 0,
          marginBottom: '10px',
        }}
        className={`px-2 ${
          isDeviceTablet(device) ? ' d-md-block px-md-0 ' : 'bg-transparent'
        }`}
      >
        {hasMultipleChildren(children)
          ? children.map((item, idx) => {
              if (idx === children.length - 1) return item;

              return <div key={'container-item-' + idx}>{item}</div>;
            })
          : children}
        {isDeviceTablet(device) && <>{footer}</>}
      </div>
      {!isDeviceTablet(device) && footer}
    </>
  );
};
