import localization from '../../../../../apps/novaflexi/public/locales/cs/application.json';
import { AdditionalDocuments } from '../../../../../apps/novaflexi/src/components/AdditionalInfo/AdditionalDocuments';
import { Beneficiary as AIBeneficiary } from '../../../../../apps/novaflexi/src/components/AdditionalInfo/Beneficiary';
import { ClientDetail as AIClientDetail } from '../../../../../apps/novaflexi/src/components/AdditionalInfo/ClientDetail';
import { Confirmation as AIConfirmation } from '../../../../../apps/novaflexi/src/components/AdditionalInfo/Confirmation';
import { Footer as AIFooter } from '../../../../../apps/novaflexi/src/components/AdditionalInfo/Footer';
import { LegalEssentials as AILegalEssentials } from '../../../../../apps/novaflexi/src/components/AdditionalInfo/LegalEssentials';
import { Title as AITitle } from '../../../../../apps/novaflexi/src/components/AdditionalInfo/Title';
import { CardGroupResponsive } from '../../../../../apps/novaflexi/src/components/ResponsiveContainers/CardGroupResponsive';
import { ContainerResponsive } from '../../../../../apps/novaflexi/src/components/ResponsiveContainers/ContainerResponsive';
import { MainSection } from '../../../../../apps/novaflexi/src/components/ResponsiveContainers/MainSection';
import { RoutesEnum } from '../../../../../apps/novaflexi/src/pages/App/routes';
import { useAdditionalForm } from './useAdditionalForm';
import { isDeviceTablet, useWindowSize } from '@flexi-monorepo/common-ui';
import {
  Client,
  ClientFlagsEnum,
  ContactAddress,
  useGetClientDetail,
} from '@flexi-monorepo/primary-api';
import {
  Alert,
  Button,
  Form,
  Heading,
  Paragraph,
} from '@george-labs.com/design-system';
import { useRouter } from 'next/router';
import React, { useCallback, useEffect, useState } from 'react';
import { FormProvider } from 'react-hook-form';

const { additionalInfo } = localization;

export const AdditionalInfo = () => {
  const { push: navigate } = useRouter();
  const { device } = useWindowSize();

  const clientDetail = useGetClientDetail({ checkAmlProfile: true });

  const [clientDetailFetched, setClientDetailFetched] =
    useState<boolean>(false);
  const methods = useAdditionalForm();
  const { handleSubmitWithValidation, setValue } = methods;
  //  workaround k odscrollování navrch stránky
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  function showAddressWarning() {
    if (clientDetail.data && !clientDetail.data?.contacts?.phone) {
      return true;
    }
    if (clientDetail?.data?.contacts?.addresses === undefined) {
      return false;
    } else {
      return (
        getAddress(
          clientDetail?.data?.contacts?.addresses,
          'SECONDARY_RESIDENCE'
        )?.country !== 'CZ'
      );
    }
  }

  const checkAddressInCz = useCallback(
    (addressCountry: string | undefined) => {
      const isTablet = isDeviceTablet(device);
      if (addressCountry !== 'CZ' && !isTablet) {
        console.log('redirect');
        navigate(RoutesEnum.CONTACTADDRESSNOTINCZ);
      }
    },
    [navigate, device]
  );

  function isHighRiskCountry(clientDetail: Client) {
    return (
      (clientDetail.flags as unknown as Array<ClientFlagsEnum>) || []
    ).includes(ClientFlagsEnum.FromHighRiskCountry);
  }

  useEffect(() => {
    if (clientDetail.data !== undefined && !clientDetailFetched) {
      setClientDetailFetched(true);
      const isFromHighRiskCountry = isHighRiskCountry(clientDetail.data);
      setValue('fromHighRiskCountry', isFromHighRiskCountry);
      checkAddressInCz(
        getAddress(
          clientDetail?.data?.contacts?.addresses,
          'SECONDARY_RESIDENCE'
        )?.country
      );
    }
  }, [checkAddressInCz, clientDetail, clientDetailFetched, setValue]);

  return (
    <div className={'no-gutters fixed-width'}>
      {showAddressWarning() && (
        <Alert icon={Alert.ICON.ERROR_CIRCLE}>
          <div className={'row'}>
            <div className={'col-9'}>
              <Heading level={3}>
                {additionalInfo.contactData.alert.header}
              </Heading>
              <Paragraph> {additionalInfo.contactData.alert.check}</Paragraph>

              <ul>
                {additionalInfo.contactData.alert.items.map(
                  (item: { value: string }) => {
                    return (
                      <Paragraph key={item.value} className={'py-0'}>
                        <li>{item.value}</li>
                      </Paragraph>
                    );
                  }
                )}
              </ul>
            </div>
            <div className={'col-3'}>
              <Button
                onClick={() => clientDetail.refetch()}
                variant={Button.VARIANT.SECONDARY}
              >
                {additionalInfo.contactData.alert.buttonLabel}
              </Button>
            </div>
          </div>
        </Alert>
      )}

      <FormProvider {...methods}>
        <Form onSubmit={handleSubmitWithValidation}>
          <ContainerResponsive
            footer={
              <AIFooter
                canContinue={!showAddressWarning()}
                backLink={RoutesEnum.MEDICALQUESTIONNAIRERESULTS}
              />
            }
          >
            <AITitle />
            <MainSection>
              <CardGroupResponsive className="px-md-4 py-md-2">
                <AIClientDetail clientDetail={clientDetail.data} />
              </CardGroupResponsive>
              <CardGroupResponsive className="px-md-4 py-md-2">
                <AIBeneficiary />
              </CardGroupResponsive>
              <CardGroupResponsive className="px-md-4 py-md-2">
                <AILegalEssentials />
              </CardGroupResponsive>
              <CardGroupResponsive className="px-md-4 py-md-2">
                <AdditionalDocuments />
              </CardGroupResponsive>
              <CardGroupResponsive className="px-md-4 py-md-2">
                <AIConfirmation clientDetail={clientDetail.data} />
              </CardGroupResponsive>
            </MainSection>
          </ContainerResponsive>
        </Form>
      </FormProvider>
    </div>
  );
};

export default AdditionalInfo;

export function getAddress(
  addresses: ContactAddress[] | undefined,
  addressType: AddressType
) {
  if (addresses === undefined) {
    return undefined;
  }

  return addresses.find((address) => {
    return address.type === addressType;
  });
}

export function getAddressString(
  addresses: ContactAddress[] | undefined,
  addressType: AddressType
) {
  if (addresses === undefined) {
    return '';
  }

  const address = getAddress(addresses, addressType);
  return `${address?.street} ${address?.streetNumber}, ${address?.city}, ${address?.zipCode}`;
}

type AddressType = 'PERMANENT_RESIDENCE' | 'SECONDARY_RESIDENCE';
