import { useWatchHighRiskCountry } from '@flexi-monorepo/novaflexi/additional-info';
import { useIsFaceliftVisible } from '../../useIsFaceliftVisible';
import { Alert, Col } from '@george-labs.com/design-system';
import { useTranslation } from 'react-i18next';

export function HighRiskCountryAlert() {
  const isFromHighRiskCountry = useWatchHighRiskCountry();
  const isFaceliftVisible = useIsFaceliftVisible();
  const { t } = useTranslation();

  if (!isFaceliftVisible) return <></>;

  return (
    <>
      {isFromHighRiskCountry && (
        <Col className={'pr-md-5'}>
          <Alert
            variant={Alert.VARIANT.WARNING}
            className={'ml-md-3'}
            heading={t('additionalInfo.highRiskCountryAlert.heading')}
          >
            {t('additionalInfo.highRiskCountryAlert.description')}
          </Alert>
        </Col>
      )}
    </>
  );
}
