import { useWindowSize, isDeviceTablet } from '@flexi-monorepo/common-ui';
import { Card } from '@george-labs.com/design-system';
import React from 'react';

interface IProps {
  children: React.ReactNode;
  className?: string;
  padded?: boolean;
}

export const CardResponsive: React.FC<IProps> = ({
  children,
  className,
  padded,
}) => {
  const { device } = useWindowSize();
  return isDeviceTablet(device) ? (
    <Card
      style={{ boxShadow: 'none' }}
      className={className}
      padded={padded}
      useCardBody={false}
    >
      {children}
    </Card>
  ) : (
    <Card className={className}>{children}</Card>
  );
};
