import { isDeviceTablet, useWindowSize } from '@flexi-monorepo/common-ui';
import { Separator } from '@george-labs.com/design-system';
import React from 'react';

interface IProps {
  children: Array<JSX.Element>;
}

export const MainSection: React.FC<IProps> = ({ children }) => {
  const { device } = useWindowSize();
  return (
    <>
      {isDeviceTablet(device) ? (
        <>
          <Separator />
          {children?.map((item, idx) => {
            if (idx === children.length - 1) return item;
            return (
              <div key={'main-section-item-' + idx}>
                {item}
                <div style={{ paddingLeft: '105px', paddingRight: '105px' }}>
                  <Separator />
                </div>
              </div>
            );
          })}
          <Separator />
        </>
      ) : (
        children
      )}
    </>
  );
};
