import localeStrings from '../../../public/locales/cs/application.json';
import { BeneficiaryFields } from '../AdditionalInfo/Beneficiary';
import { DateInput } from '@flexi-monorepo/react-hooks-form-gds-binding';
import { Alert, FormField, TextInput } from '@george-labs.com/design-system';
import React from 'react';
import { Controller, useFormContext } from 'react-hook-form';

export const OtherPersonFields = () => {
  const {
    control,
    formState: { errors },
  } = useFormContext();

  function hasError(field: BeneficiaryFields) {
    if (field === BeneficiaryFields.beneficiaryOtherPersonFirstName) {
      return errors['beneficiaryOtherPersonFirstName']?.message !== undefined;
    } else if (field === BeneficiaryFields.beneficiaryOtherPersonLastName) {
      return errors['beneficiaryOtherPersonLastName']?.message !== undefined;
    } else {
      return false;
    }
  }

  return (
    <>
      <Controller
        name={'beneficiaryOtherPersonFirstName'}
        control={control}
        render={({ field }) => (
          <FormField
            className="px-md-2 align-items-baseline"
            label={
              localeStrings.additionalInfo.inputs.beneficiary.otherPerson
                .firstName.label
            }
            labelColumnWidth={7}
            {...field}
            inputProps={{
              innerRef: field.ref,
              ref: undefined,
            }}
            // @ts-ignore
            ref={undefined}
            // @ts-ignore
            input={(inputProps) => (
              <div className=" pr-md-0 pt-2 pt-md-0">
                <TextInput {...inputProps} data-cy="jiná-osoba/jméno" />
                {hasError(
                  BeneficiaryFields.beneficiaryOtherPersonFirstName
                ) && (
                  <Alert>
                    {
                      localeStrings.additionalInfo.inputs.beneficiary
                        .otherPerson.isBeneficiaryFirstNameEmptyErrorMessage
                    }
                  </Alert>
                )}
              </div>
            )}
            required={true}
          />
        )}
      />

      <Controller
        name={'beneficiaryOtherPersonLastName'}
        control={control}
        render={({ field }) => (
          <FormField
            className="px-md-2 align-items-baseline"
            label={
              localeStrings.additionalInfo.inputs.beneficiary.otherPerson
                .lastName.label
            }
            labelColumnWidth={7}
            {...field}
            // @ts-ignore
            ref={undefined}
            // @ts-ignore
            input={(inputProps) => (
              <div className=" pr-md-0 pt-2 pt-md-0">
                <TextInput
                  {...inputProps}
                  innerRef={field.ref}
                  data-cy="jiná-osoba/příjmení"
                />
                {hasError(BeneficiaryFields.beneficiaryOtherPersonLastName) && (
                  <Alert>
                    {
                      localeStrings.additionalInfo.inputs.beneficiary
                        .otherPerson.isBeneficiaryLastNameEmptyErrorMessage
                    }
                  </Alert>
                )}
              </div>
            )}
            required={true}
          />
        )}
      />

      <DateInput
        name={'beneficiaryOtherPersonDateOfBirth'}
        data-cy="jiná-osoba/datum-narození"
        labelColumnWidth={7}
        classNameFormField={'px-md-2 align-items-baseline'}
        classNameWrapperDiv={'pr-md-0 pt-2 pt-md-0'}
        label={
          localeStrings.additionalInfo.inputs.beneficiary.otherPerson
            .dateOfBirth.label
        }
        errorMessage={
          localeStrings.additionalInfo.inputs.beneficiary.otherPerson
            .isBeneficiaryDateOfBirthEmptyErrorMessage
        }
      />
    </>
  );
};
