import { useWindowSize, isDeviceTablet } from '@flexi-monorepo/common-ui';
import { CardGroup } from '@george-labs.com/design-system';
import React from 'react';

interface IProps {
  children: React.ReactNode;
  className?: string;
}

export const CardGroupResponsive = ({ children, className }: IProps) => {
  const { device } = useWindowSize();
  return isDeviceTablet(device) ? (
    <div className={className}>{children}</div>
  ) : (
    <CardGroup>{children}</CardGroup>
  );
};
