import localeStrings from '../../../public/locales/cs/application.json';
import { useIsFaceliftVisible } from '../../useIsFaceliftVisible';
import {
  isIncomeTypeOther,
  useWatchHighRiskCountry,
  useWatchPEP,
} from '@flexi-monorepo/novaflexi/additional-info';
import {
  Alert,
  FormField,
  Paragraph,
  TextInput,
  ToggleButton,
} from '@george-labs.com/design-system';
import React from 'react';
import { Controller, useFormContext, useWatch } from 'react-hook-form';
import { useTranslation } from 'react-i18next';

export const IncomeSourceField = () => {
  const {
    control,
    formState: { errors },
  } = useFormContext();

  const { t } = useTranslation(['application']);

  const watchOtherIncome = useWatch({
    name: 'incomeType',
  });
  const watchPoliticallyExposedPerson = useWatchPEP();
  const watchFromHighRiskCountry = useWatchHighRiskCountry();

  function hasErrorPEP() {
    return errors['incomeType']?.message !== undefined;
  }

  function hasErrorOtherIncome() {
    return errors['otherIncomeType']?.message !== undefined;
  }

  const pepStrings = t(
    'additionalInfo.inputs.legalEssentials.politicallyExposedPerson.warning',
    { returnObjects: true }
  ) as { header: string; items: { key: number; value: string }[] };

  const highRiskCountryStrings = t(
    'additionalInfo.inputs.legalEssentials.fromHighRiskCountry.warning',
    { returnObjects: true }
  ) as { header: string; items: { key: number; value: string }[] };

  const isFaceliftVisible = useIsFaceliftVisible();

  return (
    <>
      {(watchPoliticallyExposedPerson ||
        (!isFaceliftVisible && watchFromHighRiskCountry)) && (
        <div className={'row px-md-5'}>
          <Alert variant={Alert.VARIANT.WARNING}>
            {watchPoliticallyExposedPerson && (
              <>
                <strong>{pepStrings.header}</strong>
                <ul>
                  {(isFaceliftVisible
                    ? pepStrings.items
                    : [pepStrings.items[0]]
                  ).map((item) => (
                    <li key={item.key} className={'py-0'}>
                      {item.value}
                    </li>
                  ))}
                </ul>
              </>
            )}
            {watchFromHighRiskCountry &&
              !watchPoliticallyExposedPerson &&
              !isFaceliftVisible && (
                <>
                  <strong>{highRiskCountryStrings.header}</strong>
                  <Paragraph>{highRiskCountryStrings.items[0].value}</Paragraph>
                </>
              )}
          </Alert>
        </div>
      )}
      <div className={'row px-md-4 '}>
        <Controller
          name={'incomeType'}
          control={control}
          render={({ field }) => (
            <FormField
              className="px-md-2 align-items-start"
              label={
                localeStrings.additionalInfo.inputs.legalEssentials.incomeType
                  .whichIncomeType.label
              }
              labelColumnWidth={7}
              {...field}
              inputProps={{
                innerRef: field.ref,
                ref: undefined,
              }}
              required
              // @ts-ignore
              input={(inputProps) => (
                <div className={' py-0 pr-md-0 pt-2 pt-md-0'}>
                  <ToggleButton
                    {...field}
                    className={'my-2'}
                    data-cy="hrazení/závislá-činnost"
                    toggled={inputProps.value === 'MAIN'}
                    type={ToggleButton.TYPE.RADIO}
                    name={'incomeType'}
                    value={
                      localeStrings.additionalInfo.inputs.legalEssentials
                        .incomeType.whichIncomeType.items[0].value
                    }
                    label={
                      localeStrings.additionalInfo.inputs.legalEssentials
                        .incomeType.whichIncomeType.items[0].label
                    }
                    //@ts-ignore
                    ref={undefined}
                  />
                  <ToggleButton
                    {...field}
                    className={'my-2'}
                    data-cy="hrazení/podnikání"
                    toggled={inputProps.value === 'SELF_EMPLOYED'}
                    type={ToggleButton.TYPE.RADIO}
                    name={'incomeType'}
                    value={
                      localeStrings.additionalInfo.inputs.legalEssentials
                        .incomeType.whichIncomeType.items[1].value
                    }
                    label={
                      localeStrings.additionalInfo.inputs.legalEssentials
                        .incomeType.whichIncomeType.items[1].label
                    }
                    //@ts-ignore
                    ref={undefined}
                  />
                  <ToggleButton
                    {...field}
                    className={'mt-2'}
                    data-cy="hrazení/jiný-zdroj"
                    toggled={inputProps.value === 'OTHER'}
                    type={ToggleButton.TYPE.RADIO}
                    name={'incomeType'}
                    value={
                      localeStrings.additionalInfo.inputs.legalEssentials
                        .incomeType.whichIncomeType.items[2].value
                    }
                    label={
                      localeStrings.additionalInfo.inputs.legalEssentials
                        .incomeType.whichIncomeType.items[2].label
                    }
                    //@ts-ignore
                    ref={undefined}
                  />
                  {hasErrorPEP() && (
                    <Alert size={Alert.SIZE.SMALL}>
                      {
                        localeStrings.additionalInfo.inputs.legalEssentials
                          .incomeType.incomeTypeErrorMessage
                      }
                    </Alert>
                  )}
                </div>
              )}
              // @ts-ignore
              ref={undefined}
            />
          )}
        />
      </div>
      {isIncomeTypeOther(watchOtherIncome) && (
        <div className={'row px-md-4'}>
          <Controller
            name={'otherIncomeType'}
            control={control}
            render={({ field }) => (
              <FormField
                className="px-md-2"
                data-cy="z-jakého-zdroje?"
                label={
                  localeStrings.additionalInfo.inputs.legalEssentials.incomeType
                    .whichIncomeType.otherIncomeType.label
                }
                inputProps={{
                  innerRef: field.ref,
                  ref: undefined,
                }}
                labelColumnWidth={7}
                optionalLabel={''}
                {...field}
                // @ts-ignore
                input={(inputProps) => (
                  <div className={'col py-0 pr-md-0 pt-2 pt-md-0'}>
                    <TextInput {...inputProps} />
                    {hasErrorOtherIncome() && (
                      <Alert>
                        {
                          localeStrings.additionalInfo.inputs.legalEssentials
                            .incomeType.whichIncomeType.otherIncomeType
                            .isOtherIncomeTypeEmptyErrorMessage
                        }
                      </Alert>
                    )}
                  </div>
                )}
                // required
                // @ts-ignore
                ref={undefined}
              />
            )}
          />
        </div>
      )}
    </>
  );
};
