import {
  BeneficiariesData,
  CreateBuffer,
  PutBeneficiariesRequest,
  SignOfferRequest,
  useAPIs,
} from '@flexi-monorepo/primary-api';
import { useMutation, useQueryClient } from '@tanstack/react-query';

export default function useSignOfferMutation() {
  const { offerApi } = useAPIs();
  const queryClient = useQueryClient();

  return useMutation<CreateBuffer, unknown, SignOfferRequest>({
    mutationKey: ['signOffer'],
    mutationFn: (requestSignOffer) => offerApi.signOffer(requestSignOffer),
    onSuccess(result) {
      queryClient.setQueryData(['signOffer'], result, {});
    },
    useErrorBoundary: true,
  });
}
