import localization from '../../../public/locales/cs/application.json';
import MedicalExaminationModalBody from '../AdditionalInfo/MedicalExaminationModalBody';
import { Button, Modal } from '@george-labs.com/design-system';
import React, { Dispatch, SetStateAction } from 'react';

interface Props {
  displayModal: boolean;
  setDisplayModal: Dispatch<SetStateAction<boolean>>;
  type: 'medicalExamination'; // v případě dalších modálů bude union stringů
}

export default function InfoModal({
  displayModal,
  setDisplayModal,
  type,
}: Props) {
  let title;
  let modalBody;

  switch (type) {
    case 'medicalExamination':
    default: {
      title =
        localization.additionalInfo.inputs.modal.medicalExamination.header;
      modalBody = <MedicalExaminationModalBody />;
    }
  }

  return (
    <Modal
      isOpen={displayModal}
      onClose={() => setDisplayModal(false)}
      closeLabel={localization.buttons.close}
      title={title}
      footer={
        <Button
          className={'mx-2'}
          variant={Button.VARIANT.PRIMARY}
          onClick={() => setDisplayModal(false)}
        >
          {localization.buttons.close}
        </Button>
      }
    >
      {modalBody}
    </Modal>
  );
}
