import {
  OffersClientAdditionalData,
  PutAdditionalClientDataRequest,
  useAPIs,
} from '@flexi-monorepo/primary-api';
import { useMutation, useQueryClient } from '@tanstack/react-query';

export default function usePutAdditionalClientDataMutation() {
  const { offerApi } = useAPIs();
  const queryClient = useQueryClient();
  return useMutation<
    OffersClientAdditionalData,
    unknown,
    PutAdditionalClientDataRequest
  >({
    mutationKey: ['clientData'],
    mutationFn: (requestParamsBeneficiaries) =>
      offerApi.putAdditionalClientData(requestParamsBeneficiaries),
    onSuccess(result) {
      queryClient.setQueryData(['clientData'], result, {});
    },
    useErrorBoundary: true,
  });
}
