import { useIsFaceliftVisible } from '../../useIsFaceliftVisible';
import { useOffer } from '@flexi-monorepo/novaflexi/offer';
import {
  CreateOfferProductIdEnum,
  RequiredDocumentsFlagsEnum,
  useGetRequestedDocuments,
} from '@flexi-monorepo/primary-api';
import { Alert, Col } from '@george-labs.com/design-system';
import { useTranslation } from 'react-i18next';

export function HighlyInsuredAlert() {
  const offer = useOffer();
  if (!offer.data?.offerResponse.id)
    throw new Error('missing offerResponse.id');
  const flags = useGetRequestedDocuments({
    productId: CreateOfferProductIdEnum.K1Fn,
    offerId: offer.data.offerResponse.id,
  })?.data?.flags;
  const { t } = useTranslation();

  if (
    flags &&
    new Set<'amlOfficerConsent'>(flags).has(
      RequiredDocumentsFlagsEnum.AmlOfficerConsent
    )
  ) {
    return (
      <Col className={'pr-md-5 mb-2'}>
        <Alert
          variant={Alert.VARIANT.WARNING}
          className={'ml-md-3'}
          heading={t('additionalInfo.highlyInsured.title')}
        >
          {t('additionalInfo.highlyInsured.description')}
        </Alert>
      </Col>
    );
  }

  return null;
}
