import {
  BeneficiariesData,
  PutBeneficiariesRequest,
  useAPIs,
} from '@flexi-monorepo/primary-api';
import { useMutation, useQueryClient } from '@tanstack/react-query';

export default function usePutBeneficiariesMutation() {
  const { offerApi } = useAPIs();
  const queryClient = useQueryClient();

  return useMutation<BeneficiariesData, unknown, PutBeneficiariesRequest>({
    mutationKey: ['beneficiaries'],
    mutationFn: (requestParamsBeneficiaries) =>
      offerApi.putBeneficiaries(requestParamsBeneficiaries),
    onSuccess(result) {
      queryClient.setQueryData(['beneficiaries'], result, {});
    },
    useErrorBoundary: true,
  });
}
