import localeStrings from '../../../../../apps/novaflexi/public/locales/cs/application.json';
import { getAddress } from './additional-info.page';
import usePutAdditionalClientDataMutation from './usePutAdditionalClientDataMutation';
import usePutBeneficiariesMutation from './usePutBeneficiariesMutation';
import useSignOfferMutation from './useSignOfferMutation';
import { thenIsRequired } from '@flexi-monorepo/common-ui';
import { useOffer } from '@flexi-monorepo/novaflexi/offer';
import {
  Address,
  CreateOfferProductIdEnum,
  PutAdditionalClientDataRequest,
  SignOfferChannelEnum,
  SignOfferProductIdEnum,
  useGetBanker,
  useGetBranches,
} from '@flexi-monorepo/primary-api';
import {
  DynamicConfig,
  getGlobalConfig,
  useAuth,
  useGetClientDetailCache,
} from '@flexi-monorepo/shared';
import { yupResolver } from '@hookform/resolvers/yup';
import {
  SubmitHandler,
  UseControllerProps,
  useForm,
  useWatch,
} from 'react-hook-form';
import useFormPersist from 'react-hook-form-persist';
import * as yup from 'yup';

export function isBeneficiaryOther(
  beneficiary: string | Date | NonNullable<boolean | undefined> | undefined
) {
  return beneficiary === 'OTHER';
}

export function isIncomeTypeOther(incomeType: string | undefined) {
  return incomeType === 'OTHER';
}

export function isPoliticallyExposedPerson(value: boolean) {
  return value;
}

export function parseDate(oldDate: string) {
  oldDate = oldDate.replace(/\s/g, '');
  const arr = oldDate.split('.').reverse();
  return arr.join('-');
}

export function trimString(value: string) {
  return value.trim();
}

const schema = yup
  .object({
    beneficiary: yup
      .string()
      .oneOf(
        localeStrings.additionalInfo.inputs.beneficiary.items.map(
          ({ value }: { value: string }) => value
        )
      )
      .required(localeStrings.validationTexts.boolean.required),
    beneficiaryOtherPersonFirstName: yup
      .string()
      .transform((val) => trimString(val))
      .when('beneficiary', {
        is: isBeneficiaryOther,
        then: thenIsRequired,
      }),
    beneficiaryOtherPersonLastName: yup
      .string()
      .transform((val) => trimString(val))
      .when('beneficiary', {
        is: isBeneficiaryOther,
        then: thenIsRequired,
      }),
    beneficiaryOtherPersonDateOfBirth: yup
      .string()
      .transform((val) => parseDate(val))
      .min(10)
      .max(10)
      .when('beneficiary', {
        is: isBeneficiaryOther,
        then: (schema) =>
          thenIsRequired(
            schema,
            localeStrings.additionalInfo.inputs.beneficiary.otherPerson
              .isBeneficiaryDateOfBirthEmptyErrorMessage
          ),
      }),
    fromHighRiskCountry: yup.boolean(),
    politicallyExposedPerson: yup
      .boolean()
      .required(
        localeStrings.additionalInfo.inputs.legalEssentials
          .politicallyExposedPerson.ispoliticallyExposedPersonEmptyErrorMessage
      ),
    incomeType: yup
      .string()
      .oneOf(
        localeStrings.additionalInfo.inputs.legalEssentials.incomeType.whichIncomeType.items.map(
          ({ value }: { value: string }) => value
        )
      )
      .when(['politicallyExposedPerson', 'fromHighRiskCountry'], {
        is: (politicallyExposedPerson: boolean, fromHighRiskCountry: boolean) =>
          politicallyExposedPerson || fromHighRiskCountry,
        then: thenIsRequired,
      }),
    otherIncomeType: yup.string().when('incomeType', {
      is: isIncomeTypeOther,
      then: yup.string().required(),
    }),
    discrepanciesInInsuranceNeeds: yup
      .boolean()
      .required(localeStrings.validationTexts.boolean.required),
    discrepanciesInInsuranceNeedsText: yup
      .string()
      .when('discrepanciesInInsuranceNeeds', {
        is: true,
        then: (schema) =>
          thenIsRequired(schema, localeStrings.validationTexts.string.required),
      }),
    otherReasonForInsurance: yup.string(),
    marketingCommunicationConsent: yup.boolean().default(false),
    thirdPartyPropositionConsent: yup
      .boolean()
      .required(
        localeStrings.additionalInfo.inputs.confirmationsAndAgreements
          .thirdPartyPropositionConsent.thirdPartyPropositionConsentErrorMessage
      )
      .when('marketingCommunicationConsent', {
        is: true,
        then: yup
          .boolean()
          .oneOf(
            [false],
            localeStrings.additionalInfo.inputs.confirmationsAndAgreements
              .thirdPartyPropositionConsent.mustBeNoErrorMessage
          ),
      }),
    confirmationEmail: yup.boolean().required().oneOf([true]),
  })
  .required();

export function prepareFlags(data: yup.InferType<typeof schema>) {
  const flags = [];
  if (data.politicallyExposedPerson === true)
    flags.push('politicallyExposedPerson');
  if (data.marketingCommunicationConsent === false)
    flags.push('marketingCommunicationConsent');
  if (data.thirdPartyPropositionConsent === true)
    flags.push('thirdPartyPropositionConsent');
  return flags;
}

export type AdditionalFormInputs = yup.InferType<typeof schema>;

export function useWatchAdditionalForm(
  props: UseControllerProps<AdditionalFormInputs>
) {
  return useWatch<AdditionalFormInputs>(props);
}

export function useWatchPEP() {
  return useWatchAdditionalForm({
    name: 'politicallyExposedPerson',
  });
}

export function useWatchHighRiskCountry() {
  return useWatchAdditionalForm({
    name: 'fromHighRiskCountry',
  });
}

export function useAdditionalForm() {
  const config: DynamicConfig = getGlobalConfig();
  const { token } = useAuth(config);
  const offer = useOffer();
  const banker: any = useGetBanker();
  const branches: any = useGetBranches();
  const putBeneficiariesMutation = usePutBeneficiariesMutation();
  const putAdditionalClientDataMutation = usePutAdditionalClientDataMutation();
  const clientDetail = useGetClientDetailCache({ checkAmlProfile: true });
  const signOfferMutation = useSignOfferMutation();
  const baseUrl = window.location.origin;
  const returnUrl = window.btoa(
    `${baseUrl}/novaflexi/sign?productId=${SignOfferProductIdEnum.K1Fn}&offerId=${offer.data?.offerResponse.id}#access_token={tokenx}`
  );
  const { handleSubmit, ...rest } = useForm<AdditionalFormInputs>({
    resolver: yupResolver(schema),
  });

  const onSubmit: SubmitHandler<AdditionalFormInputs> = async (data) => {
    if (!token) return;
    const offerId = offer.data!.offerResponse.id!; // TODO id prijde vzdy, zmenit swagger
    const flags = prepareFlags(data);
    const requestParamsBeneficiaries = {
      productId: CreateOfferProductIdEnum.K1Fn,
      offerId: offerId,
      beneficiaries: {
        beneficiaries: [
          {
            type: data.beneficiary,
            ...(isBeneficiaryOther(data.beneficiary) && {
              name: `${data.beneficiaryOtherPersonFirstName} ${data.beneficiaryOtherPersonLastName}`,
            }),
            percentage: 100,
            ...(isBeneficiaryOther(data.beneficiary) && {
              // @ts-ignore
              birthDate: new Date(data.beneficiaryOtherPersonDateOfBirth),
            }),
          },
        ],
      },
    };
    const requestParamsAdditionalClientData: PutAdditionalClientDataRequest = {
      productId: CreateOfferProductIdEnum.K1Fn,
      offerId: offerId,
      offersAdditionalClientData: {
        insured: [
          {
            type: 'MYSELF',
            contacts: {
              address: getAddress(
                clientDetail.data?.contacts?.addresses,
                'SECONDARY_RESIDENCE'
              ) as Address,
              email: clientDetail?.data?.contacts?.email,
              phone: {
                countryCallingCode: '+420',
                // @ts-ignore TODO podivat se, jaktoze tel. cislo je povinne
                phoneNumber: clientDetail.data.contacts.phone,
              },
            },
          },
        ],
        //  If client is politically exposed person or from 'high risk country', send incomeSource
        ...((data.politicallyExposedPerson === true ||
          data.fromHighRiskCountry === true) && {
          incomeSource: {
            type: data.incomeType,
            ...(isIncomeTypeOther(data.incomeType) && {
              description: data.otherIncomeType,
            }),
            other: isIncomeTypeOther(data.incomeType),
          },
        }),
        ...(data.otherReasonForInsurance && {
          otherReasonForInsurance: data.otherReasonForInsurance,
        }),
        ...(data.discrepanciesInInsuranceNeeds && {
          discrepanciesInInsuranceNeeds: data.discrepanciesInInsuranceNeedsText,
        }),
        ...(flags.length > 0 && { flags }),
      },
    };

    const beneficiariesResponse = await putBeneficiariesMutation.mutateAsync(
      // @ts-ignore
      requestParamsBeneficiaries
    );
    const additionalClientData =
      await putAdditionalClientDataMutation.mutateAsync(
        // @ts-ignore
        requestParamsAdditionalClientData
      );
    await signOfferMutation
      .mutateAsync({
        productId: SignOfferProductIdEnum.K1Fn,
        branch: banker.data?.branch
          ? banker.data.branch
          : branches.data.branches[0]?.id,
        offerId: offerId,
        channel: SignOfferChannelEnum.Tablet,
      })
      .then((response) => {
        const isValidToken =
          token?.toString().startsWith('3/') ||
          token?.toString().startsWith('adfs_');
        if (isValidToken) {
          openTKDesktop(response, offerId);
        } else {
          const tokenState = JSON.parse(atob(token?.split('.')[1]));
          if (tokenState.appid === 'urn:csas:apps:flexi_tablet') {
            openTKDesktop(response, offerId);
          } else {
            openTKTablet(response, offerId);
          }
        }
      });
    return {
      beneficiaries: beneficiariesResponse,
      additionalClientData: additionalClientData,
    };
  };

  function openTKDesktop(response: any, offerId: number) {
    window.location.href = `${
      config?.signUrlDesktop
    }?applicationId=FLEXI&bufferId=${
      response.id
    }&productId=K_1FN&offerId=${offerId}&branchId=${
      banker.data?.branch ? banker.data.branch : branches.data.branches[0]?.id
    }`;
  }

  function openTKTablet(response: any, offerId: number) {
    window.location.href = `${config?.signUrlTablet}?applicationId=FLEXI_N&productId=${SignOfferProductIdEnum.K1Fn}&offerId=${offerId}&bufferId=${response.id}&returnUrl=${returnUrl}#access_token={tokenx}`;
  }

  useFormPersist('form.additionalInfo', rest);

  return {
    handleSubmit,
    handleSubmitWithValidation: handleSubmit(onSubmit),
    ...rest,
  };
}

export default useAdditionalForm;
