import localeStrings from '../../../public/locales/cs/application.json';
import { CardResponsive } from '../ResponsiveContainers/CardResponsive';
import { isDeviceTablet, useWindowSize } from '@flexi-monorepo/common-ui';
import { getAddressString } from '@flexi-monorepo/novaflexi/additional-info';
import { Client, Contact } from '@flexi-monorepo/primary-api';
import {
  Heading,
  Icon,
  Inline,
  Paragraph,
  Row,
} from '@george-labs.com/design-system';
import React from 'react';

interface IProps {
  clientDetail: Client | undefined;
}

export const ClientDetail: React.FC<IProps> = ({ clientDetail }) => {
  const { device } = useWindowSize();

  function formatPhone(contact: Contact | undefined) {
    if (contact === undefined) {
      return ' ';
    }
    if (contact.phone === undefined) {
      return ' ';
    }
    return (
      '+420 ' +
      `${contact?.phone?.substring(0, 3)} ${contact?.phone?.substring(
        3,
        6
      )} ${contact?.phone?.substring(6, 9)}`
    );
  }

  return (
    <>
      <CardResponsive className={'px-md-5 py-md-0'}>
        <Row className={'align-items-center'}>
          <div className={'ml-3 mr-2 '}>
            <Icon
              icon={Icon.ICON.MAIL}
              color={Icon.COLOR.BLUE_400}
              size={
                isDeviceTablet(device) ? Icon.SIZE.SIZE_24 : Icon.SIZE.SIZE_16
              }
            />
          </div>
          <div className={'pt-1 h-4'}>
            <Heading level={3}>
              {localeStrings.additionalInfo.contactData.title}
            </Heading>
          </div>
        </Row>
      </CardResponsive>
      <CardResponsive className={' py-md-0'}>
        <div data-cs-mask className={'pb-2 row px-3 px-md-5'}>
          <Paragraph className={'col-md-7 px-md-5'}>
            <Inline emphasis={isDeviceTablet(device)}>
              {localeStrings.additionalInfo.contactData.phone.label}
            </Inline>
          </Paragraph>
          <Paragraph className={'col-md-5 px-md-2'}>
            <Inline emphasis={!isDeviceTablet(device)}>
              {formatPhone(clientDetail?.contacts) || ''}
            </Inline>
          </Paragraph>
        </div>
        <div data-cs-mask className={'pb-2 row px-3 px-md-5'}>
          <Paragraph className={'col-md-7 px-md-5'}>
            <Inline emphasis={isDeviceTablet(device)}>
              {localeStrings.additionalInfo.contactData.email.label}
            </Inline>
          </Paragraph>
          <Paragraph className={'col-md-5 px-md-2'}>
            <Inline emphasis={!isDeviceTablet(device)}>
              {clientDetail?.contacts?.email || ''}
            </Inline>
          </Paragraph>
        </div>
        <div data-cs-mask className={'pb-2 row px-3 px-md-5'}>
          <Paragraph className={'col-md-7 px-md-5'}>
            <Inline emphasis={isDeviceTablet(device)}>
              {
                localeStrings.additionalInfo.contactData.contactAddressInCz
                  .label
              }
            </Inline>
          </Paragraph>
          <Paragraph className={'col-md-5 px-md-2'}>
            <Inline emphasis={!isDeviceTablet(device)}>
              {getAddressString(
                // @ts-ignore
                clientDetail?.contacts?.addresses,
                'SECONDARY_RESIDENCE'
              )}
            </Inline>
          </Paragraph>
        </div>
      </CardResponsive>
    </>
  );
};
