import { ButtonBack } from '../../pages/App/ButtonBack';
import { RoutesEnum } from '../../pages/App/routes';
import { CardGroupResponsive } from '../ResponsiveContainers/CardGroupResponsive';
import { CardResponsive } from '../ResponsiveContainers/CardResponsive';
import { isDeviceTablet, useWindowSize } from '@flexi-monorepo/common-ui';
import { SubmitButton } from '@flexi-monorepo/react-hooks-form-gds-binding';
import { FormButtonGroup } from '@george-labs.com/design-system';
import React from 'react';
import { useTranslation } from 'react-i18next';

export function Footer(props?: {
  backLink?: RoutesEnum;
  canContinue: boolean;
}) {
  const { device } = useWindowSize();
  const { t } = useTranslation();
  return (
    <>
      {isDeviceTablet(device) ? (
        <CardGroupResponsive className="px-md-4">
          <CardResponsive className={'px-md-5 '}>
            <FormButtonGroup
              style={{
                display: 'flex',
                justifyContent: 'center',
                marginTop: '0',
              }}
            >
              <ButtonBack backLink={props?.backLink} />
              <SubmitButton disabled={!props?.canContinue}>
                {t('buttons.continue')}
              </SubmitButton>
            </FormButtonGroup>
          </CardResponsive>
        </CardGroupResponsive>
      ) : (
        <div className="px-2 pb-3">
          <SubmitButton disabled={!props?.canContinue}>
            {t('buttons.continue')}
          </SubmitButton>
        </div>
      )}
    </>
  );
}
