import { useSessionStorage } from 'usehooks-ts';

/**
 * Feature flag for facelift 1.10.
 *
 * feature flag subscribe to session storage key isFaceliftVisible
 */
export function useIsFaceliftVisible() {
  const [isFaceliftVisible] = useSessionStorage('isFaceliftVisible', true);

  return isFaceliftVisible;
}
